.player-answer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
    margin-top: 0;
}

.player-answer .player {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 10px;
    padding: 10px;
    color: white;
    min-width: 150px;
}

.player-answer .player-name {
    font-weight: bold;
    font-size: 1.4vw;
    margin: 0.8vw;
}

.player-answer .btn-container {
    display: flex;
    flex-direction: row;
    background-color: rgba(0, 14, 33, 0.7);
    border: 2px solid var(--primary-color-pink);
    border-radius: 8px;
    box-shadow: 0 0 8px var(--primary-color-shadow-pink);
    transition: transform 0.2s ease;
}

.btn-answer {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 3.1vw;
    height: 2.4vw;
    font-size: 1.5vw;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    transition: background-color 0.2s ease;
}

.btn-correct {
    background-color: transparent;
    color: #45a049;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.btn-incorrect {
    background-color: transparent;
    color: #e53935;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.btn-correct:hover {
    background-color: #45a049;
    color: white;
}

.btn-incorrect:hover {
    background-color: #e53935;
    color: white;
}


/* Grundstil för animationselementet */
.floating-animation {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    animation: fly-up 1s ease forwards;
    font-size: 2rem;
    pointer-events: none; /* Gör så att det inte går att klicka */
    z-index: 1000;
  }

  /* Grön animation (korrekt svar) */
.correct-animation {
    color: #28a745; /* Grön */
  }
  
  /* Röd animation (felaktigt svar) */
  .incorrect-animation {
    color: #dc3545; /* Röd */
  }
  
  /* Animationens nyckframes */
  @keyframes fly-up {
    0% {
      transform: translate(-50%, -50%) scale(1);
      opacity: 1;
    }
    50% {
      transform: translate(-50%, -200%) scale(1.2);
      opacity: 1;
    }
    100% {
      transform: translate(-50%, -300%) scale(0.8);
      opacity: 0;
    }
  }
  


@media screen and (max-width: 1300px) {
    .player-answer .player-name {
        font-size: 1.2em;
        margin: 10px;
    }
    
    .btn-answer {
        width: 40px;
        height: 30px;
        font-size: 1.5em;
    }
}